/* Pagination bar (similar to the provided UI) */

.snext-pagebar {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 0;
  border-radius: 8px;
  background: transparent;
  border: none;
  backdrop-filter: none;
}

.snext-pagebar-btn {
  background: #23273a;
  color: #b7e0ff;
  border: none;
  border-radius: 6px;
  padding: 0.5em 1em;
  font-size: 1em;
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
  outline: none;
}

.snext-pagebar-btn:hover:not(.active) {
  background: #2e3350;
  color: #fff;
}

.snext-pagebar-btn.active {
  background: #7e5ab8;
  color: #fff;
  font-weight: bold;
  cursor: default;
}

.snext-pagebar-btn.snext-pagebar-btn-arrow {
  padding: 0;
  width: 2.3em;
  height: 2.3em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

.snext-pagebar-btn.snext-pagebar-btn-arrow svg {
  font-size: 1em;
}

.snext-pagebar-btn:disabled {
  opacity: 0.45;
  cursor: not-allowed;
}

.snext-pagebar-btn:disabled:hover {
  background: #23273a;
  color: #b7e0ff;
}
