.mod-detail-container {
  display: flex;
  flex-direction: column;
  padding: 32px 0 0 0;
  max-width: 100vw;
  max-height: auto;
  min-height: 95vh;
  background: transparent;
}

.mod-detail-header {
  display: flex;
  align-items: center;
  background: linear-gradient(
    to top,
    rgba(35, 35, 58, 0.95) 10%,
    rgba(35, 35, 58, 0.1) 100%
  );
  padding: 24px 32px;
  margin: 0 24px 24px 24px;
  gap: 32px;
}

.mod-detail-img {
  width: 110px;
  height: 110px;
  border-radius: 16px;
  object-fit: cover;
  margin-right: 24px;
}

.mod-detail-title {
  font-size: 2.6em;
  font-weight: bold;
  margin: 0 0 8px 0;
  color: #fff;
}

.mod-detail-author {
  color: #14a0e1;
  font-weight: 600;
  margin-bottom: 8px;
  font-size: 1.1em;
}

.mod-detail-desc {
  color: #b3b8c5;
  font-size: 1.1em;
  margin-bottom: 8px;
}

.mod-detail-main {
  display: flex;
  flex-direction: row;
  gap: 32px;
  margin: 0 24px 60px;
  align-items: flex-start;
}

.mod-detail-main-left {
  flex: 2;
  display: flex;
  flex-direction: column;
}

.mod-detail-tabs {
  display: flex;
  gap: 28px;
  margin-bottom: 0;
  margin-top: 18px;
  font-size: 1.1em;
  font-weight: 600;
  position: relative;
}

.mod-detail-tabs-underline {
  height: 2px;
  background: #23283a;
  width: 100%;
  margin-bottom: 18px;
  margin-top: -2px;
  position: relative;
  z-index: 1;
}

.mod-detail-tabs span {
  color: #b3b8c5;
  cursor: pointer;
  padding-bottom: 6px;
  border-bottom: 2px solid transparent;
  transition: color 0.15s, border-bottom 0.15s;
  position: relative;
  z-index: 2;
}

.mod-detail-tabs .active {
  color: #14a0e1;
  border-bottom: 2px solid #14a0e1;
}

.mod-detail-content {
  flex: 2;
  background: linear-gradient(135deg, #1d1d37 0%, #23234a 100%);
  border-radius: 10px;
  padding: 20px;
}

.mod-detail-content h2 {
  font-size: 2em;
  margin-bottom: 8px;
  color: #fff;
}

.mod-detail-content p {
  color: #b3b8c5;
  font-size: 1em;
}

.mod-detail-sidebar {
  background: linear-gradient(135deg, #1d1d37 0%, #23234a 100%);
  border-radius: 10px;
  padding: 24px 24px 24px 24px;
  min-width: 260px;
  max-width: 340px;
  margin-left: 32px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-shadow: 0 4px 24px #0004;
  gap: 12px;
  position: sticky;
  top: 70px;
}

.mod-detail-install-btn {
  width: 100%;
  margin-bottom: 10px;
  background: #7e5ab8;
  color: #181c24;
  font-weight: bold;
  border-radius: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  line-height: 1;
  padding: 15px;
}

.mod-detail-install-icon {
  width: 25px;
  height: 25px;
  display: inline-block;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.65))
    drop-shadow(0 0 6px rgba(0, 0, 0, 0.55));
}

.mod-detail-install-text {
  font-size: 20px;
  line-height: 1;
}

.mod-detail-actions-row {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.mod-detail-download-btn {
  flex: 1;
  background: #2e2e57;
  color: #fff;
  border-radius: 8px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px;
}

.mod-detail-download-icon {
  margin-right: 0;
}

.mod-detail-like-btn {
  flex: 1;
  background: #2e2e57;
  color: #fff;
  border-radius: 8px;
  font-size: 1.05em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  max-width: 75px;
  min-width: 48px;
  padding: 0;
}

.mod-detail-like-count {
  font-variant-numeric: tabular-nums;
}

.mod-detail-stats {
  background: #1d1d37;
  border-radius: 12px;
  padding: 16px 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 1em;
}

.mod-detail-stat-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px 0;
  border-bottom: 1px solid #23234a;
}

.mod-detail-stat-row:last-child {
  border-bottom: none;
}

.mod-detail-stat-value {
  font-weight: 600;
  color: #eaeaff;
}

.mod-detail-dep-string {
  display: flex;
  align-items: center;
  gap: 6px;
}

.mod-detail-copy-btn {
  background: none;
  border: none;
  color: #b3b8c5;
  cursor: pointer;
  font-size: 1em;
  padding: 0 2px;
}

.mod-detail-dependants a {
  color: #3ee6b6 !important;
  font-weight: 600;
}

.mod-detail-categories {
  background: #23234a;
  border-radius: 12px;
  margin: 0px 12px 10px 12px;
}

.mod-detail-categories-title {
  font-size: 1.1em;
  font-weight: bold;
  color: #eaeaff;
  margin-bottom: 8px;
}

.mod-detail-categories-list {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.mod-detail-category-badge {
  background: #2e2e57;
  color: #b3b8c5;
  border-radius: 8px;
  padding: 4px 12px;
  font-size: 0.95em;
  font-weight: 600;
}

.mod-detail-sidebar .snext-btn {
  font-size: 1.1em;
}

.mod-detail-meta {
  background: transparent;
  color: #b3b8c5;
  font-size: 1em;
  margin-top: 12px;
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.mod-detail-meta b {
  color: #fff;
  font-weight: 600;
}

.skeleton {
  background: linear-gradient(90deg, #23283a 25%, #23283a 37%, #23283a 63%);
  border-radius: 8px;
  animation: skeleton-loading 1.2s infinite linear;
  opacity: 0.7;
}

.skeleton-text {
  margin-bottom: 10px;
}

.skeleton-btn {
  border-radius: 8px;
}

.mod-detail-github-link {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 8px;
  margin-left: 4px;
}

.mod-detail-github-link .svg-inline--fa {
  font-size: 40px;
  color: #fff;
  cursor: pointer;
  margin: 10px 0 10px 0;
  transition: color 0.3s, transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.mod-detail-github-link .svg-inline--fa:hover {
  color: #14a0e1;
  transform: scale(1.1);
}

.markdown-body {
  font-family: "Segoe UI", Arial, sans-serif;
  color: #eaeaff;
  background: none;
  line-height: 1.7;
  font-size: 1em;
  padding: 0;
}

.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  color: #fff;
  font-weight: 700;
  margin-bottom: 0.5em;
  line-height: 1.25;
}

.markdown-body h1 {
  font-size: 2em;
  border-bottom: 1px solid #35357a;
  padding-bottom: 0.3em;
}

.markdown-body h2 {
  font-size: 1.5em;
  border-bottom: 1px solid #35357a;
  padding-bottom: 0.3em;
}

.markdown-body h3 {
  font-size: 1.2em;
}

.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  font-size: 1em;
}

.markdown-body p {
  margin: 1em 0;
  color: #b3b8c5;
}

.markdown-body ul,
.markdown-body ol {
  margin: 1em 0 1em 2em;
  padding: 0;
}

.markdown-body li {
  margin-bottom: 0.3em;
}

.markdown-body a {
  color: #14a0e1;
  text-decoration: underline;
  transition: color 0.2s;
}

.markdown-body a:hover {
  color: #50c3f8;
}

.markdown-body code {
  background: #23234a;
  color: #ffea70;
  padding: 2px 6px;
  border-radius: 6px;
  font-size: 0.98em;
  font-family: "Fira Mono", "Consolas", "Menlo", monospace;
}

.markdown-body pre {
  background: #181c24;
  color: #eaeaff;
  padding: 14px 18px;
  border-radius: 8px;
  overflow-x: auto;
  font-size: 0.98em;
  margin: 1em 0;
}

.markdown-body pre code {
  background: none;
  color: inherit;
  padding: 0;
  border-radius: 0;
}

.markdown-body blockquote {
  border-left: 4px solid #35357a;
  background: #23234a;
  color: #b3b8c5;
  padding: 8px 18px;
  margin: 1em 0;
  border-radius: 6px;
}

.markdown-body hr {
  border: none;
  border-top: 1px solid #35357a;
  margin: 2em 0;
}

.markdown-body table {
  border-collapse: collapse;
  margin: 1em 0;
  width: 100%;
}

.markdown-body th,
.markdown-body td {
  border: 1px solid #35357a;
  padding: 8px 12px;
}

.markdown-body th {
  background: #23234a;
  color: #fff;
  font-weight: 600;
}

.markdown-body img {
  max-width: 100%;
  border-radius: 8px;
  margin: 1em 0;
  box-shadow: 0 2px 8px #0004;
}

@keyframes skeleton-loading {
  0% {
    background-position: -200px 0;
  }

  100% {
    background-position: calc(200px + 100%) 0;
  }
}

@media (max-width: 700px) {
  html,
  body {
    overflow-x: hidden !important;
    width: 100% !important;
    max-width: 100vw !important;
  }

  .mod-detail-container {
    padding: 20px;
    min-height: unset;
  }

  .mod-detail-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 16px 8px;
    margin: 0 0 16px 0;
    gap: 16px;
  }

  .mod-detail-img {
    width: 80px;
    height: 80px;
    margin-right: 0;
    margin-bottom: 8px;
  }

  .mod-detail-title {
    font-size: 1.5em;
    margin-bottom: 4px;
  }

  .mod-detail-author {
    font-size: 1em;
    margin-bottom: 4px;
  }

  .mod-detail-desc {
    font-size: 1em;
    margin-bottom: 6px;
  }

  .mod-detail-main {
    flex-direction: column;
    gap: 18px;
    margin: 0 0 32px 0;
  }

  .mod-detail-main-left {
    width: 100%;
  }

  .mod-detail-tabs {
    gap: 16px;
    font-size: 1em;
    margin-top: 10px;
  }

  .mod-detail-content {
    padding: 12px 6px;
    font-size: 0.98em;
  }

  .mod-detail-sidebar {
    position: static;
    top: unset;
    width: 100%;
    max-width: 350px;
    margin: 0;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 8px #0004;
  }

  .snext-btn.snext-btn-green.mod-detail-install-btn {
    display: none;
  }

  .mod-detail-install-btn {
    font-size: 1em;
    margin-bottom: 8px;
    border-radius: 8px;
  }

  .mod-detail-actions-row {
    gap: 6px;
    margin-bottom: 8px;
  }

  .mod-detail-download-btn,
  .mod-detail-like-btn {
    font-size: 1em;
    border-radius: 6px;
  }

  .mod-detail-stats {
    padding: 10px 6px;
    border-radius: 8px;
    font-size: 0.98em;
  }

  .mod-detail-categories {
    margin: 0px 0px 8px 0px;
    border-radius: 8px;
  }

  .mod-detail-categories-title {
    font-size: 1em;
    margin-bottom: 6px;
  }

  .mod-detail-categories-list {
    gap: 6px;
  }

  .mod-detail-category-badge {
    font-size: 0.93em;
    border-radius: 6px;
    padding: 3px 8px;
  }

  .mod-detail-github-link .svg-inline--fa {
    font-size: 32px;
    margin: 8px 0;
  }

  .markdown-body {
    font-size: 0.98em;
    padding: 0;
  }

  .markdown-body h1 {
    margin: 0;
  }
}

@media (max-width: 600px) {
  .mod-detail-sidebar {
    min-width: auto;
    max-width: 90%;
  }
}

@media (max-width: 500px) {
  .mod-detail-sidebar {
    min-width: auto;
    max-width: 89%;
  }
}

@media (max-width: 400px) {
  .mod-detail-sidebar {
    min-width: auto;
    max-width: 88%;
  }
}
